const { useSelect, useDispatch } = wp.data;
const { useState } = wp.element;
const { __ } = wp.i18n;
import ResponsiveSingleRangeControl from "./single-range-control";
export default function ResponsiveRangeControl({
    label,
    onChange,
    value,
    step = 1,
    max = 100,
    min = 0,
    unit = "",
    showUnit = false,
    units = ["px", "em", "rem"],
    defaultValue,
}) {
    let defaultValues = {
        Desktop: "",
        Tablet: "",
        Mobile: "",
        unit: {
            Desktop: "px",
            Tablet: "px",
            Mobile: "px",
        },
    };
    value = value ? { ...defaultValues, ...value } : defaultValues;

    const [state, setState] = useState(value);
    const [deviceType, setDeviceType] = useState("Desktop");
    let customSetPreviewDeviceType = (device) => {
        setDeviceType(device);
    };
    if (wp.data.select("core/edit-post")) {
        const theDevice = useSelect((select) => {
            const { __experimentalGetPreviewDeviceType = null } =
                select("core/edit-post");
            return __experimentalGetPreviewDeviceType
                ? __experimentalGetPreviewDeviceType()
                : "Desktop";
        }, []);
        if (theDevice !== deviceType) {
            setDeviceType(theDevice);
        }
        const { __experimentalSetPreviewDeviceType = null } =
            useDispatch("core/edit-post");
        customSetPreviewDeviceType = (device) => {
            __experimentalSetPreviewDeviceType(device);
            setDeviceType(device);
        };
    }

    const onChangeValue = (value, device) => {
        const updatedState = { ...state };
        updatedState[device] = value;
        onChange(updatedState);
        setState(updatedState);
    };
    const onChangeUnit = (value, device) => {
        const updatedState = { ...state["unit"] };
        updatedState[device] = value;
        onChange({ ...state, unit: { ...updatedState } });
        setState({ ...state, unit: { ...updatedState } });
    };
    const output = {};
    output.Mobile = (
        <ResponsiveSingleRangeControl
            device={"mobile"}
            value={state["Mobile"]}
            onChange={(size) => onChangeValue(size, "Mobile")}
            min={min}
            max={
                state["unit"]["Mobile"] === "%" ||
                state["unit"]["Mobile"] === "vw"
                    ? 100
                    : max
            }
            step={
                state["unit"]["Mobile"] === "em" ||
                state["unit"]["Mobile"] === "rem"
                    ? 0.1
                    : 1
            }
            showUnit={showUnit}
            unit={showUnit ? unit["Mobile"] : "px"}
            defaultValue={defaultValues["Mobile"]}
            responsive={true}
            onChangeUnit={(val) => onChangeUnit(val, "Mobile")}
            label={label}
            units={units}
        />
    );
    output.Tablet = (
        <ResponsiveSingleRangeControl
            device={"tablet"}
            value={state["Tablet"]}
            onChange={(size) => onChangeValue(size, "Tablet")}
            min={min}
            max={
                state["unit"]["Tablet"] === "%" ||
                state["unit"]["Tablet"] === "vw"
                    ? 100
                    : max
            }
            step={
                state["unit"]["Tablet"] === "em" ||
                state["unit"]["Tablet"] === "rem"
                    ? 0.1
                    : 1
            }
            showUnit={showUnit}
            unit={showUnit ? unit["Tablet"] : "px"}
            defaultValue={defaultValues["Tablet"]}
            responsive={true}
            onChangeUnit={(val) => onChangeUnit(val, "Tablet")}
            label={label}
            units={units}
        />
    );
    output.Desktop = (
        <ResponsiveSingleRangeControl
            device={"desktop"}
            value={state["Desktop"]}
            onChange={(size) => onChangeValue(size, "Desktop")}
            min={min}
            max={
                state["unit"]["Desktop"] === "%" ||
                state["unit"]["Desktop"] === "vw"
                    ? 100
                    : max
            }
            step={
                state["unit"]["Desktop"] === "em" ||
                state["unit"]["Desktop"] === "rem"
                    ? 0.1
                    : 1
            }
            showUnit={showUnit}
            unit={showUnit ? unit["Desktop"] : "px"}
            defaultValue={defaultValues["Desktop"]}
            responsive={true}
            onChangeUnit={(val) => onChangeUnit(val, "Desktop")}
            label={label}
            units={units}
        />
    );
    return [
        onChange && (
            <div
                className={`premium-blocks-range-control premium-blocks__base-control`}
            >
                {output[deviceType] ? output[deviceType] : output.Desktop}
            </div>
        ),
    ];
}
