import { addFilter } from "@wordpress/hooks";
import { getFontFamily } from "./typography/util";
import parse, { domToReact } from "html-react-parser";

export const gradientBackground = (value, deviceType) => {
    const {
        backgroundType,
        backgroundColor,
        backgroundRepeat,
        backgroundPosition,
        fixed,
        backgroundSize,
        gradientColorTwo,
        gradientPosition,
        gradientType,
        gradientLocationOne,
        gradientLocationTwo,
        gradientAngle,
        backgroundImageURL,
    } = value;
    let btnGrad, btnGrad2, btnbg;
    if (undefined !== backgroundType && "gradient" === backgroundType) {
        btnGrad =
            "transparent" === backgroundColor || undefined === backgroundColor
                ? "rgba(255,255,255,0)"
                : backgroundColor;
        btnGrad2 =
            undefined !== gradientColorTwo &&
            undefined !== gradientColorTwo &&
            "" !== gradientColorTwo
                ? gradientColorTwo
                : "#777";
        if ("radial" === gradientType) {
            btnbg = `radial-gradient(at ${gradientPosition}, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        } else if ("radial" !== gradientType) {
            btnbg = `linear-gradient(${gradientAngle}deg, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        }
    } else {
        btnbg = backgroundImageURL ? `url('${backgroundImageURL}')` : "";
    }
    return {
        backgroundColor:
            backgroundType === "transparent" ? "transparent" : backgroundColor,
        backgroundImage:
            backgroundType === "transparent" ? "" : gradientValue(value),
        backgroundRepeat: handleResponsiveSingleValue(
            deviceType,
            backgroundRepeat,
        ),
        backgroundPosition: handleResponsiveSingleValue(
            deviceType,
            backgroundPosition,
        ),
        backgroundSize: handleResponsiveSingleValue(deviceType, backgroundSize),
        backgroundAttachment: fixed ? "fixed" : "unset",
    };
};
export const borderCss = (value, device) => {
    return {
        borderStyle: value?.borderType,
        borderWidth: handleResponsiveMultiValue(device, value?.borderWidth),
        borderColor: value?.borderColor,
        borderRadius: handleResponsiveMultiValue(device, value?.borderRadius),
    };
};
export const parseIcon = (icon) => {
    const newIcon = icon.trim();

    const parseOptions = {
        trim: true,
        replace: ({ attribs, children, name, parent, type }) => {
            if (type !== "tag" || (!parent && name !== "svg") || !name) {
                return <></>;
            }

            // Hyphens or colons in attribute names are lost in the default
            // process of html-react-parser. Spreading the attribs object as
            // props avoids the loss. Style does need to be handled separately.
            const Tag = `${name}`;
            return <Tag {...attribs}>{domToReact(children, parseOptions)}</Tag>;
        },
    };

    return parse(newIcon, parseOptions);
};
export const paddingCss = (value, device) => {
    return {
        padding: handleResponsiveMultiValue(device, value),
    };
};
export const marginCss = (value, device) => {
    return {
        margin: handleResponsiveMultiValue(device, value),
    };
};
export const typographyCss = (value, device) => {
    const styles = {
        fontSize:
            handleResponsiveSingleValue(device, value["fontSize"]) &&
            `${handleResponsiveSingleValue(device, value["fontSize"])}${
                value?.fontSize?.unit[device]
            }`,
        fontStyle: value?.fontStyle,
        fontFamily:
            value?.fontFamily === "Default"
                ? " "
                : getFontFamily(value.fontFamily),
        fontWeight: value?.fontWeight === "Default" ? " " : value?.fontWeight,
        letterSpacing:
            handleResponsiveSingleValue(device, value?.letterSpacing) &&
            `${handleResponsiveSingleValue(device, value?.letterSpacing)}px`,
        textDecoration: value?.textDecoration,
        textTransform: value?.textTransform,
        lineHeight:
            handleResponsiveSingleValue(device, value?.lineHeight) &&
            `${handleResponsiveSingleValue(device, value?.lineHeight)}px`,
    };
    return { ...styles };
};

export const generateBlockId = (clientId) => {
    return clientId.split("-")[4];
};

export const generateCss = (styles) => {
    let styleCss = "";
    for (const selector in styles) {
        const selectorStyles = styles[selector];
        const filteredStyles = Object.keys(selectorStyles)
            .map((property) => {
                const value = selectorStyles[property];
                const valueWithoutUnits = value
                    ? value
                          .toString()
                          .replaceAll(/px|em|rem|!important|%/g, "")
                          .replaceAll(/\s/g, "")
                    : "";
                if (
                    value &&
                    !value.toString().includes("undefined") &&
                    valueWithoutUnits
                ) {
                    return `${property}: ${value};`;
                }
            })
            .filter((style) => !!style)
            .join("\n");
        styleCss += `${selector}{
                    ${filteredStyles}
                }\n`;
    }

    return styleCss;
};

export const filterJsCss = (styles) => {
    const asArray = Object.entries(styles);

    const filtered = asArray.filter(([property, value]) => {
        const valueWithoutUnits = value
            ? value
                  .toString()
                  .replaceAll(/px|em|rem|!important|%/g, "")
                  .replaceAll(/\s/g, "")
            : "";

        return (
            value &&
            !value.toString().includes("undefined") &&
            valueWithoutUnits
        );
    });
    const filteredStyles = Object.fromEntries(filtered);

    return filteredStyles;
};

export const handleResponsiveSingleValue = (device, val) => {
    let data;
    if (val && val.Desktop !== "") {
        data = val.Desktop;
    }
    if (
        val &&
        val.Tablet !== "" &&
        (device === "Tablet" || device === "Mobile")
    ) {
        data = val.Tablet;
    }
    if (val && val.Mobile !== "" && device === "Mobile") {
        data = val.Mobile;
    }
    return data;
};

export const handleResponsiveMultiValue = (device, val) => {
    let data;

    if (val && Object.values(val.Desktop).some((x) => x !== "")) {
        let { top, right, bottom, left } = val["Desktop"];
        data = `${top ? top : "0"}${
            typeof val.unit === "object" ? val.unit["Desktop"] : "px"
        } ${right ? right : "0"}${
            typeof val.unit === "object" ? val.unit["Desktop"] : "px"
        } ${bottom ? bottom : "0"}${
            typeof val.unit === "object" ? val.unit["Desktop"] : "px"
        } ${left ? left : "0"}${
            typeof val.unit === "object" ? val.unit["Desktop"] : "px"
        }`;
    }
    if (
        val &&
        Object.values(val.Tablet).some((x) => x !== "") &&
        (device === "Tablet" || device === "Mobile")
    ) {
        let { top, right, bottom, left } = val["Tablet"];
        data = `${top ? top : "0"}${
            typeof val.unit === "object" ? val.unit["Tablet"] : "px"
        } ${right ? right : "0"}${val.unit ? val.unit["Tablet"] : "px"} ${
            bottom ? bottom : "0"
        }${typeof val.unit === "object" ? val.unit["Tablet"] : "px"} ${
            left ? left : "0"
        }${typeof val.unit === "object" ? val.unit["Tablet"] : "px"}`;
    }
    if (
        val &&
        Object.values(val.Mobile).some((x) => x !== "") &&
        device === "Mobile"
    ) {
        let { top, right, bottom, left } = val["Mobile"];
        data = `${top ? top : "0"}${
            typeof val.unit === "object" ? val.unit["Mobile"] : "px"
        } ${right ? right : "0"}${
            typeof val.unit === "object" ? val.unit["Mobile"] : "px"
        } ${bottom ? bottom : "0"}${
            typeof val.unit === "object" ? val.unit["Mobile"] : "px"
        } ${left ? left : "0"}${
            typeof val.unit === "object" ? val.unit["Mobile"] : "px"
        }`;
    }
    return data;
};

export const videoBackground = (
    backgroundType,
    videoSource,
    videoURL,
    bgExternalVideo,
) => {
    if (backgroundType == "video") {
        if (videoSource == "local") {
            if (videoURL) {
                return (
                    <div className="premium-blocks-video-bg-wrap">
                        <video
                            className="premium-blocks-video-bg"
                            autoPlay
                            muted
                            loop
                        >
                            <source src={videoURL} />
                        </video>
                    </div>
                );
            }
        }
        if (videoSource == "external") {
            if (bgExternalVideo) {
                let video = bgExternalVideo,
                    src = "";
                if (video.match("youtube|youtu.be")) {
                    let id = 0;
                    if (video.match("embed") && video.split(/embed\//)[1]) {
                        id = video.split(/embed\//)[1].split('"')[0];
                    } else if (video.split(/v\/|v=|youtu\.be\//)[1]) {
                        id = video
                            .split(/v\/|v=|youtu\.be\//)[1]
                            .split(/[?&]/)[0];
                    }
                    src =
                        "//www.youtube.com/embed/" +
                        id +
                        "?playlist=" +
                        id +
                        "&iv_load_policy=3&enablejsapi=1&disablekb=1&autoplay=1&mute=1&controls=0&showinfo=0&rel=0&loop=1&wmode=transparent&widgetid=1";
                } else if (video.match("vimeo.com")) {
                    let id = video
                        .split(/video\/|https:\/\/vimeo\.com\//)[1]
                        .split(/[?&]/)[0];
                    src =
                        "//player.vimeo.com/video/" +
                        id +
                        "?autoplay=1&loop=1&title=0&byline=0&portrait=0";
                }
                return (
                    <div className="premium-blocks-video-bg-wrap">
                        <iframe
                            src={src}
                            frameBorder="0"
                            allowFullScreen
                        ></iframe>
                    </div>
                );
            }
        }
    }
};

export const animationAttr = (data) => {
    if (
        typeof data !== "undefined" &&
        typeof data.name !== "undefined" &&
        data.openAnimation
    ) {
        return { "data-premiumanimation": JSON.stringify(data) };
    } else {
        return {};
    }
};
export const gradientValue = (value) => {
    const {
        backgroundType,
        backgroundColor,
        gradientColorTwo,
        gradientPosition,
        gradientType,
        gradientLocationOne,
        gradientLocationTwo,
        gradientAngle,
        backgroundImageURL,
    } = value;

    let btnGrad, btnGrad2, btnbg;
    if (undefined !== backgroundType && "gradient" === backgroundType) {
        btnGrad =
            "transparent" === backgroundColor || undefined === backgroundColor
                ? "rgba(255,255,255,0)"
                : backgroundColor;
        btnGrad2 =
            undefined !== gradientColorTwo &&
            undefined !== gradientColorTwo &&
            "" !== gradientColorTwo
                ? gradientColorTwo
                : "#777";
        if ("radial" === gradientType) {
            btnbg = `radial-gradient(at ${gradientPosition}, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        } else if ("radial" !== gradientType) {
            btnbg = `linear-gradient(${gradientAngle}deg, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        }
    } else {
        btnbg = backgroundImageURL ? `url('${backgroundImageURL}')` : "none";
    }
    return btnbg;
};

export const getBorderCss = (value, deviceType) => {
    return {
        "border-style": value?.borderType,
        "border-width": `${handleResponsiveMultiValue(
            deviceType,
            value?.borderWidth,
        )}`,
        "border-color": `${value?.borderColor}`,
        "border-radius": `${handleResponsiveMultiValue(
            deviceType,
            value?.borderRadius,
        )}`,
    };
};

export const getPaddingCss = (value, deviceType) => {
    return {
        padding: `${handleResponsiveMultiValue(deviceType, value)}`,
    };
};

export const getMarginCss = (value, deviceType) => {
    return {
        margin: `${handleResponsiveMultiValue(deviceType, value)}`,
    };
};

export const getTypographyCss = (value, deviceType) => {
    return {
        "font-size": `${handleResponsiveSingleValue(
            deviceType,
            value?.fontSize,
        )}${value?.fontSize?.unit[deviceType]}`,
        "font-style": value?.fontStyle,
        "font-family":
            value.fontFamily !== "Default"
                ? getFontFamily(value?.fontFamily)
                : "",
        "font-weight": value.fontWeight !== "Default" ? value?.fontWeight : "",
        "letter-spacing": `${handleResponsiveSingleValue(
            deviceType,
            value?.letterSpacing,
        )}${value?.letterSpacing?.unit}`,
        "line-height": `${handleResponsiveSingleValue(
            deviceType,
            value?.lineHeight,
        )}${value?.lineHeight?.unit}`,
        "text-decoration": value?.textDecoration,
        "text-transform": value?.textTransform,
    };
};

export const getBackgroundCss = (value, deviceType, additionalWord = "") => {
    const {
        backgroundType,
        backgroundColor,
        backgroundRepeat,
        backgroundPosition,
        fixed,
        backgroundSize,
        gradientColorTwo,
        gradientPosition,
        gradientType,
        gradientLocationOne,
        gradientLocationTwo,
        gradientAngle,
        backgroundImageURL,
    } = value;
    let btnGrad, btnGrad2, btnbg;
    if (undefined !== backgroundType && "gradient" === backgroundType) {
        btnGrad =
            "transparent" === backgroundColor || undefined === backgroundColor
                ? "rgba(255,255,255,0)"
                : backgroundColor;
        btnGrad2 =
            undefined !== gradientColorTwo &&
            undefined !== gradientColorTwo &&
            "" !== gradientColorTwo
                ? gradientColorTwo
                : "#777";
        if ("radial" === gradientType) {
            btnbg = `radial-gradient(at ${gradientPosition}, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        } else if ("radial" !== gradientType) {
            btnbg = `linear-gradient(${gradientAngle}deg, ${btnGrad} ${gradientLocationOne}%, ${btnGrad2} ${gradientLocationTwo}%)`;
        }
    } else {
        btnbg = backgroundImageURL ? `url('${backgroundImageURL}')` : "";
    }
    if (backgroundType == "") {
        return;
    }
    return {
        "background-color": `${
            backgroundType === "transparent" ? "transparent" : backgroundColor
        }${additionalWord}`,
        "background-image": `${gradientValue(value)}${additionalWord}`,
        "background-repeat": `${handleResponsiveSingleValue(
            deviceType,
            backgroundRepeat,
        )}${additionalWord}`,
        "background-position": `${handleResponsiveSingleValue(
            deviceType,
            backgroundPosition,
        )}${additionalWord}`,
        "background-size": `${handleResponsiveSingleValue(
            deviceType,
            backgroundSize,
        )}${additionalWord}`,

        "background-attachment": `${
            fixed ? "fixed" : "unset"
        }${additionalWord}`,
    };
};

export const createElementFromHTMLString = (
    htmlString,
    contentRef,
    name,
    blockId,
) => {
    if (contentRef && contentRef.current) {
        let container = contentRef.current.querySelector(
            `#premium-${name}-svg-${blockId}`,
        );
        container.innerHTML = htmlString;
        return container.firstElementChild;
    }
};

export const addSVGAttributes = (
    svgHTML,
    contentRef,
    name,
    blockId,
    attributesToAdd = {},
    attributesToRemove = [],
) => {
    const svgNode = createElementFromHTMLString(
        svgHTML,
        contentRef,
        name,
        blockId,
    );
    if (!svgNode) {
        return "";
    }

    Object.keys(attributesToAdd).forEach((key) => {
        svgNode.setAttribute(key, attributesToAdd[key]);
    });

    attributesToRemove.forEach((key) => {
        svgNode.removeAttribute(key);
    });
    return svgNode.outerHTML;
};

export const isPremiumBlock = (blockName) => {
    return blockName.startsWith("premium/");
};

const addVariationIdAttribute = (settings, name) => {
    if (!isPremiumBlock(name)) {
        return settings;
    }
    if (typeof settings.attributes !== "undefined") {
        settings.attributes = Object.assign(settings.attributes, {
            vUniqueId: {
                type: "string",
                default: "",
            },
        });
    }
    return settings;
};

addFilter(
    "blocks.registerBlockType",
    "pbg/variation-id-attribute",
    addVariationIdAttribute,
);

export const getBlockId = (blockName, attributes) => {
    let uniqueId = "";
    switch (blockName) {
        case "premium/container":
            if (attributes.block_id && attributes.block_id !== "") {
                uniqueId = `premium-container-${attributes.block_id}`;
            }
            break;
        default:
            uniqueId = attributes.blockId || "";
            break;
    }

    return uniqueId;
};

export const generateRandomIdForInput = () => {
    // Id from 8 numbers and letters.
    const id = Math.random().toString(36).substring(2, 10);
    return id;
};
