const { __ } = wp.i18n;
import PremiumSizeUnits from "../premium-size-units";
import PremiumRange from "./range-control";
const { useSelect, useDispatch } = wp.data;
const { useState } = wp.element;

import Responsive from "../responsive";
export default function ResponsiveSingleRangeControl({
    device = "device",
    onChange,
    value,
    step = 1,
    max = 100,
    min = 0,
    unit = "",
    onChangeUnit,
    showUnit = false,
    units = ["px", "em", "rem"],
    className = "",
    label,
    help,
    defaultValue,
    responsive = false,
}) {
    const [deviceType, setDeviceType] = useState("Desktop");
    let customSetPreviewDeviceType = (device) => {
        setDeviceType(device);
    };
    if (wp.data.select("core/edit-post")) {
        const theDevice = useSelect((select) => {
            const { __experimentalGetPreviewDeviceType = null } =
                select("core/edit-post");
            return __experimentalGetPreviewDeviceType
                ? __experimentalGetPreviewDeviceType()
                : "Desktop";
        }, []);
        if (theDevice !== deviceType) {
            setDeviceType(theDevice);
        }
        const { __experimentalSetPreviewDeviceType = null } =
            useDispatch("core/edit-post");
        customSetPreviewDeviceType = (device) => {
            __experimentalSetPreviewDeviceType(device);
            setDeviceType(device);
        };
    }

    return [
        onChange && (
            <div
                className={`premium-blocks-range-control premium-blocks__base-control`}
            >
                {label && (
                    <header>
                        <div
                            className={`premium-slider-title-wrap`}
                            style={{ display: "flex", alignItems: "center" }}
                        >
                            {label && (
                                <span className="customize-control-title premium-control-title">
                                    {label}
                                </span>
                            )}
                            {responsive && (
                                <Responsive
                                    onChange={(newDevice) =>
                                        setDeviceType(newDevice)
                                    }
                                />
                            )}
                        </div>
                        {showUnit && (
                            <PremiumSizeUnits
                                units={units}
                                activeUnit={unit}
                                onChangeSizeUnit={(newValue) =>
                                    onChangeUnit(newValue)
                                }
                            />
                        )}
                    </header>
                )}
                <PremiumRange
                    value={undefined !== value ? value : ""}
                    onChange={(size) => onChange(size)}
                    min={min}
                    max={max}
                    step={step}
                    defaultValue={defaultValue}
                />
            </div>
        ),
    ];
}
